import React from 'react';
import { connect } from 'react-redux';
import mobiscroll from '@mobiscroll/react';
import '@mobiscroll/react/dist/css/mobiscroll.min.css';
import { completeDoable, deleteDoable } from '../actions';
import Doable from './Doable';

export function Area (props) {

  const getStages = () => ({
    left: [
      {
        percent: 30,
        icon: 'checkmark',
        color: 'green',
        action: function ({target}) {
          props.completeDoable(target.getAttribute('data-id'))
          mobiscroll.toast({
              message: 'Done'
          });
        }
      }
    ],
    right: [
      {
        percent: -40,
        icon: 'remove',
        color: 'red',
        action: function ({target}, inst) {
          props.deleteDoable(target.getAttribute('data-id'))
          mobiscroll.toast({
              message: 'Deleted'
          });
        }
      }
    ]
  });

  return (
    <mobiscroll.Form theme="material" lang="de">
      <mobiscroll.FormGroup {...styles.container}>
          <mobiscroll.FormGroupTitle {...styles.header}>{props.area}</mobiscroll.FormGroupTitle>
          <mobiscroll.Listview
              theme="material"
              lang="de"
              itemType={Doable}
              data={props.doables}
              sortable={{handle: 'left'}}
              stages={getStages()}
              undoText="Undo"
              navigateOnDrop={true}
              swipe={({target}) => props.doables.find( doable => doable.id === +target.getAttribute('data-id') ).status !== 'complete' || 'left'}
              onSortStart={props.disableSwiping}
              onSortEnd={props.enableSwiping}
              onSlideStart={props.disableSwiping}
              onSlideEnd={props.enableSwiping}
              {...styles.list}
          />
      </mobiscroll.FormGroup>
    </mobiscroll.Form>
  );
};

const styles = {
  container: {
    style: {
      maxWidth: '800px',
      height: '100vh',
      boxSizing: 'border-box',
      background: '#dbb076',
      margin: '0 auto',
      color: 'rgba(0,0,0,0.87)'
    }
  },
  header: {
    style: {
      color: 'rgba(0,0,0,0.87)'
    }
  },
  list: {
    style: {
      backgroundColor: '#dbb076'
    }
  },
  frm: {
    style: {
      backgroundColor: 'transparent',
      fontSize: '5rem'
    }
  }
}

const mapStateToProps = state => state;

const mapDispatchToProps = dispatch => ({
  completeDoable: id => dispatch(completeDoable(id)),
  deleteDoable: id => dispatch(deleteDoable(id))
});

const mergeProps = (state, actions, ownProps) => ({
  ...ownProps,
  area: state.areas[ownProps.area].name,
  doables: state.doables.filter( doable => doable.area === ownProps.area ),
  ...actions
})

export default connect(mapStateToProps, mapDispatchToProps, mergeProps)(Area);
