import React from 'react';

const Doable = ({item}) => {
  document.querySelectorAll('.mbsc-lv-item div:not([class])').forEach( div => div.style.display = 'none');
  const {id, name, status} = item;
  return <li data-id={id} {...styles(status)}>{name}</li>;
}

const styles = status => {
  switch (status) {
    case 'complete':
      return {
        style: {
          textDecoration: 'line-through',
          color:  'rgba(0,0,0,0.42)',
          backgroundColor: '#dbb076',
          opacity: '0.5'
        }
      }

    default:
      return {
        style: {
          backgroundColor: '#dbb076'
        }
      }
  }
}

export default Doable;