import React, { Component } from 'react';
import { connect } from 'react-redux';
import SwipeableView from 'react-swipeable-views';
import DoableForm from './components/DoableForm';
import ReviewSection from './components/ReviewSection';
import AreaSection from './components/AreaSection';
import Stepper from './components/Stepper';
import { updatePageIndex } from './actions';

export class App extends Component {

  state = {
    swipeable: true,
    sectionIndex: 0,
    reviewIndex: 0,
    areaIndex: 0
  }

  swipeToggler = {
    enableSwiping: () => this.setState({...this.state, swipeable: true}),
    disableSwiping: () => this.setState({...this.state, swipeable: false})
  }

  handleChangeSectionIndex = index => {
    this.props.updatePageIndex(index, true);
  }

  handleChangeIndex = index => {
    this.props.updatePageIndex(index);
  }

  render() {
    const sections = [<DoableForm key="entrySection" quickEntry autofocus />];
    if (this.props.reviewables.length) sections.push(<ReviewSection key="reviewSection" reviewables={[...this.props.reviewables]} handleChangeIndex={this.handleChangeIndex} />);
    if (this.props.areas.length) sections.push(<AreaSection key="areaSection" disabled={!this.state.swipeable} swipeToggler={this.swipeToggler} />);

    return (
      <React.Fragment>
        <SwipeableView
          axis="y"
          containerStyle={{height: `${window.innerHeight}px`}}
          disabled={!this.state.swipeable}
          enableMouseEvents
          onChangeIndex={this.handleChangeIndex}
        >
          {sections}
        </SwipeableView>
        {/* <Stepper sectionCount={sections.length} /> */}
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  reviewables:  state.doables.filter( doable => doable.area === ''),
  areas: [...new Set(state.doables.map( doable => doable.area ).filter( area => area !== '' ))]
});

const mapDispatchToProps = dispatch => ({
  updatePageIndex: (...args) => dispatch(updatePageIndex(...args))
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
